import React, { useState, useContext } from "react";
import { RegionSelectorContext } from "./RegionSelectorContext";

const RegionSelector = () => {
  const [region, setRegion] = useContext(RegionSelectorContext);
  const changeRegion = (e) => {
    setRegion(e.target.value);
  };

  return (
    <div className="section-center">
      <h3>Region Selector</h3>
      <label for="region-select">Choose a region: </label>
      <div className="select">
        <select onChange={changeRegion} name="region" id="region-select">
          <option value="none" selected disabled hidden>
            Select Region
          </option>
          <option value="2">Kanto</option>
          <option value="3">Johto</option>
          <option value="4">Hoenn</option>
          <option value="5">Sinnoh</option>
          <option value="8">Unova</option>
          <option value="12">Kalos</option>
          <option value="16">Alola</option>
          <option value="20">Galar</option>
        </select>
      </div>
    </div>
  );
};

export default RegionSelector;
