import React from "react";
import "./App.css";
import { Link } from "react-router-dom";
import CardGenerator from "./CardGenerator";

import example1 from "./../static/example1.png";
import example2 from "./../static/example2.png";
import example3 from "./../static/example3.png";
import example4 from "./../static/example4.png";

function Home() {
  return (
    <div>
      <h2 className="section-headers">Home</h2>
      <div className="description">
        <p>
          Design your dream get-well card or holiday card with Pokemon Card
          Generator! Using photos fetched from Unsplash API and PokeAPI, you can
          work in-browser to create your ideal card featuring your favorite
          pokepal!
        </p>
        <Link to="/cardgenerator">
          <button>Design a Card</button>
        </Link>
        <p className="above-margin">
          Feel free to search the pokemon database for your favorite pokemon to
          add to the card.
        </p>
        <Link to="/pokedex">
          <button>Search Pokemon</button>
        </Link>
      </div>

      <div className="images-collection">
        <img
          src={example1}
          alt="Pokemon Card Generator card example 1, Image of Ludicolo over background image of concert with the caption, 'Party time!'"
          className="image-size-smaller"
        />
        <img
          src={example2}
          alt="Pokemon Card Generator card example 2, image of chimchar over background image of vase of lavender flowers with the caption, 'get better soon'"
          className="image-size-smaller"
        />
        <img
          src={example3}
          alt="Pokemon Card Generator card example 3, image of bulbasaur over background image of shrine at Chureito Pagoda with Mt. Fuji in the background, and the caption, 'visit again soon!'"
          className="image-size-smaller"
        />
        <img
          src={example4}
          alt="Pokemon Card Generator card example 4, image of illumise over background image of close-up rose with the caption, 'feel better!'"
          className="image-size-smaller"
        />
      </div>
    </div>
  );
}

export default Home;
