import React from "react";
import axios from "axios";
import Bluebird from "bluebird";

import { trackPromise } from "react-promise-tracker";

export const FetchPokemonList = async ({
  pokemon,
  setPokemon,
  region,
  loading,
  setLoading,
}) => {
  try {
    const { data: dataN } =
      await axios.get(`https://pokeapi.co/api/v2/pokedex/${region}/

  `);

    // const pokemonEntryNumberArray = await Bluebird.map(dataN.pokemon_entries)

    const detailedPokeArr = await Bluebird.map(
      dataN.pokemon_entries,
      async (poke, i) => {
        // if (i % 2 === 0) return;
        const { data } = await axios.get(poke.pokemon_species.url);

        const { data: finalData } = await axios.get(
          data.varieties[0].pokemon.url
        );

        finalData["entry_number"] = poke.entry_number;

        return finalData;
      }
    );

    // Old method - using limit
    //     const data = await fetch(`https://pokeapi.co/api/v2/pokemon?limit=75
    // `);

    //     const pokemonData = await data.json();

    //     const enhancedPokeData = await Promise.all(
    //       pokemonData.results.map(async (poke) => {
    //         let extraData = await fetch(poke.url);
    //         extraData = await extraData.json();
    //         poke.details = extraData;
    //         return poke;
    //       })
    //     );

    setPokemon(detailedPokeArr);
    setLoading(false);
  } catch (error) {
    console.log("error", error);
  }
};
