import React, { useState, createContext } from "react";

export const CardGeneratorContext = createContext();

export const CardGeneratorProvider = (props) => {
  const [backgroundPhotoURL, setBackgroundPhotoURL] = useState("forest");
  const [pokemonSelect, setPokemonSelect] = useState("bulbasaur");
  const [pokemonFetchStatus, setPokemonFetchStatus] = useState({});
  const [backgroundPhotoFetchStatus, setBackgroundPhotoFetchStatus] = useState(
    {}
  );
  return (
    <CardGeneratorContext.Provider
      value={[
        backgroundPhotoURL,
        setBackgroundPhotoURL,
        pokemonSelect,
        setPokemonSelect,
        pokemonFetchStatus,
        setPokemonFetchStatus,
        backgroundPhotoFetchStatus,
        setBackgroundPhotoFetchStatus,
      ]}
    >
      {props.children}
    </CardGeneratorContext.Provider>
  );
};
