import React, { useState, useContext, useEffect, useRef } from "react";
import { render } from "react-dom";
import { Stage, Layer, Image, Text, Transformer } from "react-konva";
import useImage from "use-image";

import { CardGeneratorContext } from "./CardGeneratorContext";

// import ExportHandler from "./ExportHandler";

const CardCanvas = ({ cardText, textColor }) => {
  const [
    backgroundPhotoURL,
    setBackgroundPhotoURL,
    pokemonSelect,
    setPokemonSelect,
    pokemonFetchError,
    setPokemonFetchError,
  ] = useContext(CardGeneratorContext);

  const [pokemonArray, setPokemonArray] = useState([pokemonSelect]);

  const [pokemon] = useImage(pokemonSelect);

  const [backgroundImage] = useImage(backgroundPhotoURL);
  const [isDragging, setIsDragging] = useState(false);
  const [isPokemonDragging, setIsPokemonDragging] = useState(false);
  const [pokemonWidth, setPokemonWidth] = useState(250);
  const [pokemonHeight, setPokemonHeight] = useState(250);
  const [imageX, setImageX] = useState(0);
  const [imageY, setImageY] = useState(0);
  const [pokemonX, setPokemonX] = useState(130);
  const [pokemonY, setPokemonY] = useState(325);
  const [textX, setTextX] = useState(60);
  const [textY, setTextY] = useState(100);

  const handleDragStart = (e) => {
    e.target.setAttrs({
      shadowOffset: {
        x: 5,
        y: 5,
      },
      shadowBlur: 10,
      scaleX: 1.1,
      scaleY: 1.1,
    });
  };

  const PokemonPicture = ({ shapeProps, isSelected, onSelect, onChange }) => {
    const shapeRef = React.useRef();
    const transformerRef = React.useRef();

    React.useEffect(() => {
      if (isSelected) {
        transformerRef.current.nodes([shapeRef.current]);
        transformerRef.current.getLayer().batchDraw();
      }
    }, [isSelected]);

    return (
      <React.Fragment>
        <Image
          image={pokemon}
          width={pokemonWidth}
          height={pokemonHeight}
          onClick={onSelect}
          onTap={onSelect}
          ref={shapeRef}
          {...shapeProps}
          draggable
          x={pokemonX}
          y={pokemonY}
          onDragEnd={(e) => {
            onChange({
              ...shapeProps,
              x: e.target.x(),
              y: e.target.y(),
              width: pokemonWidth,
              height: pokemonHeight,
            });
          }}
          onTransformEnd={(e) => {
            const node = shapeRef.current;
            const scaleX = node.scaleX();
            const scaleY = node.scaleY();

            node.scaleX(1);
            node.scaleY(1);

            onChange({
              ...shapeProps,
              x: node.x(),
              y: node.y(),
              width: Math.max(5, node.width() * scaleX) || 1,
              height: Math.max(node.height() * scaleY) || 1,
            });
          }}
        />
        {isSelected && (
          <Transformer
            ref={transformerRef}
            boundBoxFunc={(oldBox, newBox) => {
              if (newBox.width < 5 || newBox.height < 5) {
                return oldBox;
              }
              return newBox;
            }}
          />
        )}
      </React.Fragment>
    );
  };
  const [rectangles, setRectangles] = React.useState(pokemonArray);
  const [selectedId, selectShape] = React.useState(null);

  const checkDeselect = (e) => {
    // deselect when clicked on empty area
    // change something here so that when you click on NOT this layer it deselects
    const clickedOnEmpty = e.target === e.target.getStage();
    // console.log(backgroundImage.src);
    const clickedOnBackground =
      e.target.attrs.image?.src === backgroundImage?.src;
    // console.log(clickedOnBackground);
    if (clickedOnEmpty || clickedOnBackground) {
      selectShape(null);
    }
  };

  // const handleExport = (stageRef) => {
  //   if (!stageRef.current) return;
  //   console.log(stageRef.current);
  //   const uri = stageRef.current.toDataURL();
  //   window.open(uri);

  //   downloadURI(uri, "stage.png");
  // };

  // function downloadURI(uri, name) {
  //   var link = document.createElement("a");
  //   link.download = name;
  //   link.href = uri;
  //   document.body.appendChild(link);
  //   link.click();
  //   document.body.removeChild(link);
  //   console.log(link);
  // }

  // const ExportHandler = ({ stageRef }) => {
  //   return (
  //     <div>
  //       <h3>Export Handler</h3>

  //     </div>
  //   );
  // };

  const stageRef = useRef(null);

  return (
    <div className="column-canvas card-canvas">
      <div className="card-generator-border">
        <Stage
          // width={window.innerWidth}
          width={500}
          height={600}
          ref={stageRef}
          // height={window.innerHeight}
          onMouseDown={checkDeselect}
          onTouchStart={checkDeselect}
        >
          {/* The background image from Unsplash */}
          <Layer id={"bg-image"}>
            <Image
              image={backgroundImage}
              x={imageX}
              y={imageY}
              draggable
              onDragStart={() => {
                setIsDragging(true);
              }}
              onDragEnd={(e) => {
                setIsDragging(false);
                setImageX(e.target.x());
                setImageY(e.target.y());
              }}
            ></Image>
          </Layer>

          {/* Test: Pokemon layer with transformations */}
          <Layer>
            {/*
            {pokemonArray.map((poke, i) => {
              console.log(shapeProps);
              return (
                <PokemonPicture
                  key={i}
                  shapeProps={poke}
                  isSelected={1 === selectedId}
                  onSelect={() => {
                    selectShape(1);
                    console.log("when clicking on object: ", selectedId);
                    console.log(pokemonHeight);
                  }}
                  onChange={(newAttrs) => {
                    const pokes = pokemonArray.slice();
                    pokes[i] = newAttrs;
                    setRectangles(pokes);
                    // console.log(newAttrs);
                  }}
                ></PokemonPicture>
              );
            })} */}
            <PokemonPicture
              key={1}
              shapeProps={pokemon}
              isSelected={1 === selectedId}
              onSelect={() => {
                selectShape(1);
              }}
              onChange={(newAttrs) => {
                setPokemonWidth(newAttrs.width);
                setPokemonHeight(newAttrs.height);
                console.log(newAttrs.width, newAttrs.height);
                setPokemonX(newAttrs.x);
                setPokemonY(newAttrs.y);
              }}
            ></PokemonPicture>
          </Layer>
          {/* The text */}
          <Layer>
            <Text
              draggable
              x={textX}
              y={textY}
              fontSize={50}
              fontFamily={"Sofia Pro"}
              text={cardText}
              fill={textColor}
            ></Text>
          </Layer>

          {/* The Pokemon Image */}
          {/* <Layer>
          <Image
            image={pokemon}
            scaleX={isPokemonDragging ? 1.1 : 1}
            scaleY={isPokemonDragging ? 1.1 : 1}
            x={pokemonX}
            y={pokemonY}
            draggable
            onDragStart={(e) => {
              setIsPokemonDragging(true);

              handleDragStart(e);
            }}
            onDragEnd={(e) => {
              setIsPokemonDragging(false);
              setPokemonX(e.target.x());
              setPokemonY(e.target.y());
            }}
          ></Image>
        </Layer> */}
        </Stage>
      </div>
      {/* <button onClick={() => handleExport(stageRef)}>Export</button> */}
    </div>
  );
};

export default CardCanvas;
