import React, { useState, useContext, useEffect } from "react";
import { CardGeneratorContext } from "./CardGeneratorContext";

import axios from "axios";

export default function SearchPokemon() {
  useEffect(() => {
    const url = "https://pokeapi.co/api/v2/pokemon/bulbasaur";

    axios
      .get(url)
      .then((response) => {
        setPokemonSelect(response.data.sprites.front_default);
        setPokemonFetchStatus(response.status);
      })
      .catch(({ request }) => {
        setPokemonFetchStatus(request.status);
      });
  }, []);
  const [query, setQuery] = useState("bulbasaur");
  // Setting image search URL coming from CardGeneratorContext controlled by Context
  const [
    backgroundPhotoURL,
    setBackgroundPhotoURL,
    pokemonSelect,
    setPokemonSelect,
    pokemonFetchStatus,
    setPokemonFetchStatus,
  ] = useContext(CardGeneratorContext);

  const searchForPokemon = async (e) => {
    e.preventDefault();

    if (query.length === 0) return;
    const url = `https://pokeapi.co/api/v2/pokemon/${query}`;

    axios
      .get(url)
      .then((response) => {
        console.log(response);
        setPokemonSelect(response.data.sprites.front_default);
        setPokemonFetchStatus(response.status);
      })
      .catch(({ request }) => {
        console.log(request);
        setPokemonFetchStatus(request.status);
      });
  };

  return (
    <div className="search-forms search-pokemon-form">
      <form onSubmit={searchForPokemon}>
        <label>Pokemon Search</label>
        <input
          type="text"
          name="query"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder={`Search for Pokemon`}
        ></input>
        <button type="submit">Search</button>
      </form>
    </div>
  );
}
