import React, { useEffect, useState } from "react";

const TextInputForCanvas = ({ cardText, setCardText }) => {
  useEffect(() => {
    setText(cardText);
  }, []);

  const [text, setText] = useState("");

  const updateText = (e) => {
    setText(e.target.value);
    setCardText(e.target.value);
  };

  const setCanvasText = (e) => {
    e.preventDefault();
    setCardText(text);
  };

  return (
    <div>
      <form onSubmit={setCanvasText}>
        <label>Text Input</label>
        <input
          type="text"
          name="cardtext"
          value={text}
          onChange={(e) => {
            updateText(e);
            // setCardText(e);
          }}
        ></input>
      </form>
    </div>
  );
};

export default TextInputForCanvas;
