import React from "react";
import { usePromiseTracker } from "react-promise-tracker";
import Loader from "react-loader-spinner";

const LoaderGUI = () => {
  // const { promiseInProgress } = usePromiseTracker();
  // return promiseInProgress && <h2>I'm loading!</h2>;
  return (
    <div
      style={{
        width: "100%",
        height: "100",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Loader type="Grid" color="lightcoral" height="100" width="100"></Loader>
    </div>
  );
};

export default LoaderGUI;
