import React, { useState } from "react";

const ColorInputForCanvas = ({ textColor, setTextColor }) => {
  const [color, setColor] = useState("");

  const updateColor = (e) => {
    setColor(e.target.value);
    setTextColor(e.target.value);
  };

  return (
    <div className="color-input">
      <label for="colorselect">Select text color: </label>
      <input
        type="color"
        id="colorselect"
        name="colorselect"
        value={textColor}
        onChange={(e) => {
          updateColor(e);
          console.log(e);
        }}
      ></input>
      {/* <div>{textColor}</div> */}
    </div>
  );
};

export default ColorInputForCanvas;
