import React from "react";
import "./PokemonCard.css";

const PokemonCard = ({ pokemon }) => {
  const types = pokemon.types.reduce((acc, type, i) => {
    return acc + type.type.name + (i === 1 ? "" : " -");
  }, "");

  return (
    <div className="pokemon-card">
      <h1 className="pokemon-card-title">
        {pokemon.entry_number}. {pokemon.name.toUpperCase()}
      </h1>
      <div className="pokemon-card-picture-border">
        <div className={`pokemon-card-picture ${types} `}>
          <div className="pokemon-card-picture-background">
            <img src={pokemon.sprites.front_default}></img>
          </div>
        </div>
      </div>
      {console.log(pokemon)}
      <div className="pokemon-card-types">
        <div className={`pokemon-card-types-one ${pokemon.types[0].type.name}`}>
          {pokemon.types[0].type.name}
        </div>
        {pokemon?.types[1]?.type?.name && (
          <div
            className={`pokemon-card-types-two ${pokemon?.types[1]?.type?.name}`}
          >
            {pokemon?.types[1]?.type?.name}
          </div>
        )}
      </div>
      <div>
        {pokemon.moves.map((move, i) => {
          if (i < 3) {
            return (
              <p>
                {i + 1}. {move.move.name.replace("-", " ")}
              </p>
            );
          }
        })}
      </div>
    </div>
  );
};

export default PokemonCard;
