import React, { useState } from "react";
import ReactDOM from "react-dom";
import Nav from "./Nav";
import Home from "./Home";
import Shop from "./Shop";
import PokemonList from "./PokemonList";
import ItemDetail from "./ItemDetail";
import AddPokemon from "./AddPokemon";
import CardGenerator from "./CardGenerator";
import Pokedex from "./Pokedex";
import { PokemonProvider } from "./PokemonContext";
import { RegionSelectorProvider } from "./RegionSelectorContext";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { CardGeneratorProvider } from "./CardGeneratorContext";

import BackgroundImage from "./BackgroundImage";
import SearchPhotos from "./SearchPhotos";

import "../styles/main.css";

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <PokemonProvider>
        <div className="app">
          <Nav />

          <Switch>
            <Route path="/" exact component={Home} />

            {/* <Route path="/about" component={About} /> */}
            {/* <RegionSelectorProvider>
              <Route path="/database" component={Database} />
            </RegionSelectorProvider>
            <Route path="/database/:pageNumber" component={Database} /> */}
            <Route path="/shop" exact component={Shop} />
            <Route path="/shop/:id" component={ItemDetail} />
            <Route path="/pokemonlist" component={PokemonList} />
            <CardGeneratorProvider>
              <RegionSelectorProvider>
                <Route path="/cardgenerator" component={CardGenerator} />

                <Route path="/pokedex" exact component={Pokedex} />
              </RegionSelectorProvider>
            </CardGeneratorProvider>
          </Switch>
        </div>
      </PokemonProvider>
    </Router>
  );
}

export default App;
