import React, { useState, useContext, useEffect } from "react";
import { createApi, toJson } from "unsplash-js";
import { CardGeneratorContext } from "./CardGeneratorContext";

import axios from "axios";

export default function SearchPhotos() {
  const [query, setQuery] = useState("christmas tree");
  const [photoURL, setPhotoURL] = useState("");
  // Setting image search URL coming from CardGeneratorContext controlled by Context
  const [
    backgroundPhotoURL,
    setBackgroundPhotoURL,
    pokemonSelect,
    setPokemonSelect,
    pokemonFetchStatus,
    setPokemonFetchStatus,
    backgroundPhotoFetchStatus,
    setBackgroundPhotoFetchStatus,
  ] = useContext(CardGeneratorContext);

  const unsplash = createApi({
    accessKey: process.env.REACT_APP_API_KEY,
  });

  useEffect(() => {
    const fetchBackgroundImage = async () => {
      const background_image = await unsplash.search.getPhotos({
        query: query,
      });

      (await background_image.response.results[0])
        ? setBackgroundPhotoURL(
            background_image.response.results[0].urls.regular
          )
        : setBackgroundPhotoFetchStatus({ state: false });
    };
    fetchBackgroundImage();
  }, []);

  const searchForPhotos = async (e) => {
    e.preventDefault();
    console.log("Submitted");

    if (query.length === 0) return;

    const background_image = await unsplash.search.getPhotos({
      query: query,
    });

    background_image.response.results[0]
      ? setBackgroundPhotoURL(background_image.response.results[0].urls.regular)
      : setBackgroundPhotoFetchStatus({ state: false });

    // const url = `https://api.unsplash.com/search/photos/?page=1&per_page=10&query=${query}`;

    // const backgroundImageArray = axios.get(url).then((response) => {
    //   console.log(response);
    // });
    // console.log(background_image);

    // setPhotoURL(background_image.response.results[0].urls.small);

    //   .then((result) => {
    //     console.log(result.response.results[0].urls.regular);
    //     setPhotoURL(result.response.results[0].urls.regular);
    //   });
  };

  return (
    <div className="search-forms search-photo-form">
      <form onSubmit={searchForPhotos}>
        <label>Image Search</label>
        <input
          type="text"
          name="query"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder={`Try 'Forest'`}
        ></input>
        <button type="submit">Search</button>
      </form>
    </div>
  );
}
