import React, { useState, useContext, useRef } from "react";
import SearchPhotos from "./SearchPhotos";
import SearchPokemon from "./SearchPokemon";
import CardCanvas from "./CardCanvas";
import { CardGeneratorContext } from "./CardGeneratorContext";
import TextInputForCanvas from "./TextInputForCanvas";
import ColorInputForCanvas from "./ColorInputForCanvas";
import CanvasHowToUse from "./CanvasHowToUse";
import ExportHandler from "./ExportHandler";

const CardGenerator = () => {
  const [
    backgroundPhotoURL,
    setBackgroundPhotoURL,
    pokemonSelect,
    setPokemonSelect,
    pokemonFetchError,
    setPokemonFetchError,
    backgroundPhotoFetchStatus,
  ] = useContext(CardGeneratorContext);

  // Canvas modifiers section states
  const [cardText, setCardText] = useState("Happy holidays!");
  const [textColor, setTextColor] = useState("#ffaa00");

  // Set stage ref for export
  const stageRef = useRef(null);

  return (
    <div>
      <div className="card-generator-page-container">
        <div className="card-generator-page-contents-container">
          <div className="column-third">
            <h2 className="section-headers card-generator-header">
              Card Generator
            </h2>
            <p className="description">
              Generate a get-well card or a card for the holidays with the
              Pokemon Database and a photo from Unsplash!
            </p>
            <SearchPhotos />
            {backgroundPhotoFetchStatus.state === false ? (
              <div className="pokemon-fetch-error">
                No photos were found with this query.
              </div>
            ) : null}
            <SearchPokemon />
            {pokemonFetchError === 404 ? (
              <div className="pokemon-fetch-error">
                Please enter valid Pokemon name.
              </div>
            ) : null}
            <section className="canvas-modifier-section">
              <h3>Modify the Canvas</h3>
              <TextInputForCanvas
                cardText={cardText}
                setCardText={setCardText}
              />
              <ColorInputForCanvas
                textColor={textColor}
                setTextColor={setTextColor}
              />
            </section>
            <CanvasHowToUse />
            <ExportHandler stageRef={stageRef} />
          </div>
          <CardCanvas
            cardText={cardText}
            textColor={textColor}
            stageRef={stageRef}
          />
        </div>
      </div>
    </div>
  );
};

export default CardGenerator;
