import React, { useContext } from "react";
import "./App.css";
import { Link } from "react-router-dom";
import { PokemonContext } from "./PokemonContext";

import icon from "./../static/pokeball-icon.png";

const Nav = () => {
  const [pokemon, setPokemon] = useContext(PokemonContext);
  const navStyle = {
    color: "white",
  };
  return (
    <nav>
      <Link to="/">
        <div className="navbar-right">
          <img
            alt="pokeball"
            src={icon}
            style={{
              width: "80px",
              height: "80px",
            }}
          />
          <h1 className="navbar-title">Pokemon Card Generator</h1>
        </div>
      </Link>

      <ul className="nav-links">
        <Link style={navStyle} to="/">
          <li>Home</li>
        </Link>
        <Link style={navStyle} to="/cardgenerator">
          <li>Make a Card!</li>
        </Link>
        <Link style={navStyle} to="/pokedex">
          <li>Pokedex</li>
        </Link>

        {/* <Link style={navStyle} to="/shop">
          <li>Shop</li>
        </Link> */}

        {/* <Link style={navStyle} to="/pokemonlist">
          <li>Pokemon List: {pokemon.length}</li>
        </Link> */}
      </ul>
    </nav>
  );
};

export default Nav;
